/* eslint-disable react/prop-types */
import React, { Component } from 'react';
import Link from 'gatsby-link';
import { Logo, Humberger } from './';
import { viewport } from '../constants';

export default class MobileHeader extends Component {
  constructor(props) {
    super(props);

    this.state = {
      open: false,
    };
  }

  handleClick = (e) => {
    e.preventDefault();
    this.setState({ open: !this.state.open });
  };

  render() {
    const { open } = this.state;

    return (
      <div className="root">
        <style jsx>{`
          .root {
            position: fixed;
            top: 0;
            right: 0;
            left: 0;
            z-index: 900;
            height: 60px;
            background: #fff;
            box-shadow: 0 2px 3px rgba(0, 0, 0, 0.16);
          }

          .root > div {
            position: absolute;
            top: 0;
          }

          .left {
            left: 0;
          }

          .left :global(a) {
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;
            padding: 0 15px;
            height: 60px;
            color: #000;
            text-decoration: none;
          }

          .left :global(svg) {
            width: 16px;
            height: 24px;
          }

          .left span {
            display: block;
            padding-left: 10px;
            font-family: 'Poppins', sans-serif;
            font-size: 0.75rem;
          }

          .right {
            right: 0;
          }

          @media (${viewport.sm}) {
            .root {
              display: none;
            }
          }
        `}</style>

        <div className="left">
          <Link to="/">
            <Logo />
            <span>react drip form</span>
          </Link>
        </div>

        <div className="right">
          <Humberger
            active={open}
            onClick={this.handleClick}
          />
        </div>
      </div>
    );
  }
}



